import React from 'react';
import ReactDOM from 'react-dom/client';
import App from "./components/App";
import reportWebVitals from './reportWebVitals';
import {HashRouter, Route, Routes} from 'react-router-dom';
import DashBoard from './components/Dashboard';
import { QueryClient, QueryClientProvider } from 'react-query';
import Gallery from './components/MemesGallery';
import GroupsView from './components/Groups/GroupsView';

const queryClient = new QueryClient();

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <QueryClientProvider client={queryClient}>
      <HashRouter>
        <Routes>
          <Route path="/" element={<App authen={'sigin'} />} />
          <Route path="/signup" element={<App authen={'signup'} />} />
          <Route path="/dashboard" element={<DashBoard />} />
          <Route path="/groupmanage" element={<GroupsView />} />
          <Route path="/memesgallery" element={<Gallery/>} />
        </Routes>
      </HashRouter>
    </QueryClientProvider>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
